import {GetWordsList,SaveTeacherWords} from '@/api/teacher'
import UniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue'
export default {
	data(){
		return{
			loaded:false,
			asklist:[],
			tcid:0,
			content:'',
			pageindex:1,
			pagesize:10,
			pageCount:0,
			moreStatus:'more',
			isFocus:false,
			bottomHeight:0
		}
	},
	components:{
		UniLoadMore
	},
	onReachBottom(e){
		this.pageindex++
		this.moreList()
	},
	onLoad: function(option) {
		this.tcid = option.tcid
		uni.showLoading({
		    title: '加载中'
		});
		this.loadPage()
	},
	methods: {
		setBottomHeight(e){
			this.isFocus = true
			this.bottomHeight = e.detail.height 
		},
		setNoHeight(){
			this.isFocus = false
			this.bottomHeight = 0
		},
		moreList(){
			if(this.getParameters.pageindex > this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'loading'
				this.loadPage('more')
			}
		},
		loadPage(status){
			if(status=='more'){
				GetWordsList({
					tcid:this.tcid,
					pageindex:this.pageindex,
					pagesize:this.pagesize
				}).then(res=>{
					this.moreStatus = 'more'
					if(res.result){
						this.asklist = this.asklist.concat(res.data)
					}
					this.loaded = true
				})
			}else{
				this.pageindex = 1
				GetWordsList({
					tcid:this.tcid,
					pageindex:this.pageindex,
					pagesize:this.pagesize
				}).then(res=>{
					if(res.result){
						this.pageCount = res.pageCount
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
						this.asklist = res.data
					}else{
						this.asklist = []
					}
					this.loaded = true
					uni.hideLoading();
				})
			}
		},
		submit(){
			uni.showLoading({
			    title: '正在提交中'
			});
			SaveTeacherWords({
				tcid:this.tcid,
				content:this.content
			}).then(res=>{
				uni.hideLoading();
				if(res.result){
					uni.showToast({
					    title: '提交成功',
					    duration: 2000,
						icon:'none'
					});
					this.loadPage()
					this.content = ''
				}else{
					uni.showToast({
					    title: res.msg,
					    duration: 2000,
						icon:'none'
					});
				}
			})
		}
	}
}
